.add {
	position: relative;
	padding-top: 10px;
	text-align: center;
	margin: 30px;
	height: 120px;
	background: linear-gradient(90deg, #8408cd 0%, #527eb3 100% );
	border-radius: 10px;
}
.add .fa-window-close{
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 20px;
	cursor: pointer;
}